#projects {
  background: #6d7f75;
  padding-bottom: 40px;
  width: 100%;
  text-align: center;
}

#projects h2 {
  margin: 25px 0 25px;
  width: 100%;
  text-align: center;
  font-weight: 700;
  font-size: 1.8em;
  color: #1e3426;
}

.projects-wrapper {
  max-width: 1200px;
  margin: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;;
}

.each-project {
  position: relative;
  margin: 10px;
  max-width: 47%;
  border: 2px solid #18261d;
  overflow: hidden;
  min-width: 440px;
}

.each-project .project-image {
  display: block;
  width: 100%;
  height: 100%;
}

.each-project:hover .project-image {
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
  -webkit-transform: scale(1.3);
      -ms-transform: scale(1.3);
          transform: scale(1.3)
}

.article-layer {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  opacity: 0;
  background: rgb(206, 206, 206);;
  -webkit-transition: opacity 1000ms;
  -o-transition: opacity 1000ms;
  transition: opacity 1000ms;
}

.article-layer:hover {
  opacity: .9;
}

.article-layer h3 {
  padding: 25px 0 15px 0;
  margin-bottom: 20px;
  text-align: center;
  font-size: 2em;
  font-weight: 700;
}

.article-layer p {
  width: 90%;
  margin: 0 auto;
  text-align: left;
}

.article-layer .project-icons {
  width: 60px;
  padding: 10px;
}

.article-layer .project-button {
  position: relative;
  top: 30px;
  height: 35px;
  width: 100px;
  border: none;
  border-radius: 5px;
  background: #000;
  color: #FFF;
  cursor: pointer;
  outline: none;
}



